import React, { useState } from 'react'
import { connect } from 'react-redux'
import {logg} from './loginaction'

const Login = (props) => {
    const [isShow,setShow]=useState(false);
    const log = () => {
        props.dispatch(logg())
        props.history.push('/home')
    }
    return (
        <div style={{textAlign:'center',marginTop:'40px'}}>
            <div><input type='text' placeholder='请输入淘宝账号'/></div>
            <div style={{marginTop:'20px'}}><input type='number' placeholder='请输入密码'/></div>
            <button style={{marginTop:'20px'}} onClick={log}>登录</button>              
        </div>
     )
}

const mapStateToProps = (state) => {
    return {
        username: state.userinfo       
    };
}

export default connect(mapStateToProps)(Login)
