import React from 'react';
import {TabBar} from 'antd-mobile';
import {withRouter} from 'react-router-dom'
import 'antd-mobile/dist/antd-mobile.css';

class Mytab extends React.Component {
  constructor(props) {
    super();
    this.state = {
      selectedTab: '',
    };
  }


  render() {
    const {pathname}=this.props.location;
    return (
      <div style={{ position: 'fixed', width: '100%', bottom: 0 }}>
        <TabBar
          unselectedTintColor="#949494"
          tintColor="#33A3F4"
          barTintColor="white"
        >

          {/* 第一页 */}
          <TabBar.Item
            title="首页"
            key="Home1"
            icon={
                <i className='iconfont icon-home'></i>
            }
            selectedIcon={
                <i className='iconfont icon-home1'></i>
            }
            selected={pathname === '/home'}
            // badge={1}
            onPress={() => {
                this.props.history.push('/home');       
            }}
            data-seed="logId"
          >

          </TabBar.Item>

          {/* 购物车*/}
          <TabBar.Item
            icon={
                <i className='iconfont icon-collect'></i>
            }
            selectedIcon={
                <i className='iconfont icon-collect'></i>
            }
            title="收藏"
            key="collect"
            // badge={'new'}
            selected={pathname === '/collect'}
            onPress={() => {
              this.props.history.push('/collect');
            }}
            data-seed="logId1"
          >
           
          </TabBar.Item>

          {/*订单列表 */}
          <TabBar.Item
            icon={
                <i className='iconfont icon-list'></i>
            }
            selectedIcon={
                <i className='iconfont icon-list1'></i>
            }
            title="订单列表"
            key="list"
            selected={pathname === '/list'}
            onPress={() => {
              this.props.history.push('/list');
            }}
          >
           {/* 我的 */}
          </TabBar.Item>
          <TabBar.Item
            icon={
                <i className='iconfont icon-my'></i>
            }
            selectedIcon={
                <i className='iconfont icon-my1'></i>
            }
            title="我的"
            key="my"
            selected={pathname === '/my'}
            onPress={() => {
                this.props.history.push('/my');
            }}
          >           
          </TabBar.Item>

        </TabBar>
      </div>
    );
  }
}

export default withRouter(Mytab);