import React,{useEffect} from 'react'
import {withRouter} from 'react-router-dom';
import './Goodslist.css'
import {getgoods} from './Goodsaction'
import {connect} from 'react-redux'

const Goodslist = (props) => {
    useEffect(() => {
        props.dispatch(getgoods());
    }, []);
         

    return <div className='big'>
        {
            props.mydata.map((item,idx)=><div className='word' key={idx}  onClick={ ()=>{props.history.push({pathname:`/details/:${item.id}`,state:item ,index:item.id })} } ><img src={item.img} /> <h5>{item.title}</h5>
            <h6 style={{color:'orange'}}>￥&nbsp;{item.price}</h6></div>)     
        }
    </div>
}
const mapStataeToProps=(state)=>{
    return {
        mydata:state.goods
    }
}
export default withRouter(connect(mapStataeToProps)(Goodslist))
