import React from 'react';
import './Search.css'
import {withRouter} from 'react-router-dom'


const Search=(props)=>{
  return (<div className="top">
       <ul>
         <li style={{fontSize: 30,color:'white',marginLeft:'-30px',marginTop:'-13px',paddingTop:'15px'}} className='iconfont icon-taobao'></li>
         <li className="search">   
           <input
            placeholder="搜索宝贝"
            onClick={()=>{props.history.push('/ser')}}
            style={{backgroundColor:'coral',marginLeft:'20px',marginTop:'2px',width:'280px',height:'30px'}}
          />
        </li>
      </ul>
      
    </div>);
}


export default withRouter(Search);