import React from 'react'
import {connect} from 'react-redux'


const Details = (props) => {
    const my=props.location.state;
    let aa='iconfont icon-shoucang';
    let i=0,j=0;
    const imgs=()=>{
        for(i=0;i<props.showgoods.length;i++){
            if(my.id==props.showgoods[i].id){
             return 'iconfont icon-shoucang1'
            }
        }
        return 'iconfont icon-shoucang'
    }      

    return (
        <div>
            {
                <div>
                    <i className='iconfont icon-return' onClick={()=>{props.history.push('/home')}} style={{fontSize:'30px',position:'absolute',top:'10px',left:'10px'}}></i>

                    <img src={my.img} style={{width:'100%',height:'400px',positiion:'relative',top:'0px'}} />      

                    <h4>{my.title}</h4>
                    <h4 style={{color:'orange'}}>￥&nbsp;{my.price}</h4> 

                    <div style={{position:'fixed',marginTop:'110px',zIndex:'2',backgroundColor:'rgba(240, 235, 226, 0.925)',width:'100%',height:'60px'}}>

                        <div style={{display:'inline-block',marginLeft:'18px'}}>
                            <div className='iconfont icon-dianpu' style={{marginLeft:'5px'}}></div>
                            <div style={{display:'inline-block',height:'50px',paddingTop:'4px',marginLeft:'3px',marginTop:'10px'}}>店铺</div>
                        </div>

                        <div style={{display:'inline-block',marginLeft:'8px'}}>
                            <div className='iconfont icon-kefu' style={{marginLeft:'7px'}}></div>
                            <div style={{display:'inline-block',marginTop:'13px',marginLeft:'5px'}}>客服</div>
                        </div>
                        
                        <div
                        style={{display:'inline-block',marginLeft:'8px',marginTop:'12px'}}>

                            <div className={imgs()} onClick={(e)=>{
                                if(props.username){
                                    props.dispatch({ 
                                        type:'ADD_GOODS',
                                        index:{img:<img src={my.img} style={{width:'100px',height:'120px'}}/>,id:my.id}
                                        })             
                                }else if(!props.username){
                                    props.history.push('/login')
                                }
                                                          
                            }} 
                            style={{display:'inline-block',marginLeft:'10px'}}></div>
                            <div style={{marginLeft:'8px',marginTop:'13px'}}>收藏</div>
                        </div>

                        <div style={{marginTop:'-90px',marginLeft:'165px'}}>
                            <div style={{display:'inline-block',height:'60px',width:'100px',backgroundColor:'yellowgreen',textAlign:'center',fontSize:'14px',color:'white',lineHeight:'60px',marginTop:'-10px'}}>加入购物车</div>
                            <div style={{display:'inline-block',height:'60px',width:'100px',backgroundColor:'coral',textAlign:'center',fontSize:'14px',color:'white',lineHeight:'60px'}}>立即购买</div>    
                        </div>
                    </div>        
                </div>
            }
        </div>
    )
}

const mapStateToProps = (state) => {
    console.log(state.colgoods);
    return {
        username: state.userinfo.username,
        showgoods:state.colgoods,
        mydata:state.goods
    }
}

export default connect(mapStateToProps)(Details)


