import React from 'react'
import Mytab from './Mytab'
import {connect} from 'react-redux'

const Collect = (props) => {
   
    return (
        <div style={{height:'1200px'}}>
            {
                props.showgoods.map((item,idx)=><li key={idx} style={{listStyle:'none',marginTop:'10px',backgroundColor:'lightgrey'}}>
                    {item.img}
                    <button onClick={()=>{props.dispatch({
                        type:'DEL_GOODS',
                        index:idx
                    })}} style={{marginLeft:'20px'}}>取消收藏</button>
                </li>)
            }
           <Mytab />
        </div>
    )
}

const mapStataeToProps=(state)=>{
    console.log(state)
    return {
        showgoods:state.colgoods,
        // mydata:state.goods
    }
}
export default connect(mapStataeToProps)(Collect)