import React from 'react';
import 'antd-mobile/dist/antd-mobile.css';
import {Provider} from 'react-redux'
import store from './Taobao/store'
import Home from './Taobao/Home'
import Collect from './Taobao/Collect'
import List from './Taobao/List'
import My from './Taobao/My'
import Ser from './Taobao/Ser'
import Goodslist from './Taobao/Goodslist'
import Details from './Taobao/Details'
import Login from './Taobao/Login'
import {BrowserRouter as Router,Route,Switch} from 'react-router-dom';

const App=()=>{
  return <Provider store={store}>
      <Router> 
            <Switch>
              <Route path='/home' component={Home}/>
              <Route path='/collect' component={Collect}/>
              <Route path='/list' component={List}/>
              <Route path='/my' component={My}/>
              <Route path='/ser' component={Ser}/>
              <Route path='/goodslist' component={Goodslist}/>
              <Route path='/login' component={Login}/>
              <Route path='/details/:id' component={Details}/>
              <Route component={Home} />
          </Switch>
        </Router>
  </Provider>
 
}
export default App;









