import React from 'react'
import { Carousel} from 'antd-mobile';

class Car extends React.Component{
  constructor(){
    super();
    this.state = {
      data: [],
      imgHeight: 200
    }
  }
  
  componentDidMount() {
    setTimeout(() => {
      this.setState({
        data: ['i1/860222288/O1CN01Swg7IU1SlwpBYMtX4_!!860222288-0-alimamazszw','i1.1650269249/O1CN01Tq0SdS2IC5qI01ZC5_!!1650269249-0-alimamazszw','i2/6000000006053/O1CN01HffIb91uaK0uSiWvR_!!6000000006053-0-octopus','i4/6000000001122/O1CN01h1tZ5p1K9v0sfGQ4D_!!6000000001122-0-octopus']
      });
    }, 100)
  }
  render() {
    return (
        <Carousel autoplay={true} infinite={true} >
          {this.state.data.map(val => (
            <a
              key={val}
              href="http://www.alipay.com"
              style={{ display: 'inline-block', width: '100%', height: this.state.imgHeight }}
            >
              <img
                src={`https://aecpm.alicdn.com/imgextra/${val}.jpg`}
                style={{ width: '100%', verticalAlign: 'top' ,margin:0}}
                onLoad={() => {
                  window.dispatchEvent(new Event('resize'));
                  this.setState({ imgHeight: 'auto' });
                }}
              />
            </a>
          ))} 
        </Carousel>
    );
  }
}
 export default Car

