let goods=[];

const Collectreducer = (state=goods,action) => {
    switch(action.type){
        case 'ADD_GOODS':
            return [...state,action.index]
        case 'DEL_GOODS':
            let arr=[...state];
            arr.splice(action.index,1);
            return arr
        default:
            return state;
    }
        
}

export default Collectreducer
